import React from 'react';
import Link from 'next/link';

const Product = ({ title, description, price }) => {
  return (
    <div>
      <Link href='/selectedProduct'>
        <h3>{title}</h3>
        <div>{description}</div>
        <h4>Price: {price}</h4>
      </Link>
    </div>
  );
};

export default Product;
